<template>
  <AppRow permission="DASHBOARD_ADMINISTRATION">
    <v-col cols="12">
      <DatePicker
        label="Диапазон дат"
        @save="saveDates"
      />
    </v-col>

    <v-col>
      <ApolloQuery
        :query="require('../graphql/queries/getDashboardInfo.gql')"
        :variables="{
          dateFrom,
          dateTo,
        }"
      >
        <template v-slot="{ result: { error: dashboardError, data: dashboardData }, isLoading: dashboardIsLoading }">
          <!-- Error -->
          <AppNotification
            v-if="dashboardError"
            :message="dashboardError.message"
          />

          <!-- Result -->
          <v-row v-else-if="dashboardData || dashboardIsLoading === 1">
            <v-col
              v-for="item of cards"
              :key="item.value"
              cols="12"
              :sm="item.value === 'promotions' ? '12' : '6'"
              :md="item.value === 'ngr' || item.value === 'houseEdge' ? '6' : '4'"
            >
              <InfoCard
                :loading="dashboardIsLoading"
                :item="item"
                :data="dashboardData"
                property="getDashboardInfo"
                :icon="item.value === 'houseEdge' ? '%' : 'RUB'"
              >
                <template v-if="item.value === 'promotions'">
                  <v-btn
                    icon
                    absolute
                    top
                    right
                    @click="show = !show"
                  >
                    <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                  </v-btn>
                  <v-expand-transition>
                    <div v-show="show">
                      <v-divider />

                      <v-card-text>
                        <v-list-item
                          v-for="expense of expenses"
                          :key="expense.value"
                          two-line
                        >
                          <v-list-item-content>
                            <v-list-item-title>{{ expense.text }}</v-list-item-title>
                            <v-list-item-subtitle>{{ dashboardData ? dashboardData.getDashboardInfo[expense.value] : '?' }}</v-list-item-subtitle>
                          </v-list-item-content>
                        </v-list-item>
                      </v-card-text>
                    </div>
                  </v-expand-transition>
                </template>
              </InfoCard>
            </v-col>

            <v-col
              cols="12"
              class="text-center text-h5"
            >
              График
            </v-col>

            <v-col cols="12">
              <PieBase :data="chartData" />
            </v-col>

            <v-col
              cols="12"
              class="text-center text-h5"
            >
              Игровые режимы
            </v-col>

            <v-col>
              <v-card
                :loading="dashboardIsLoading === 1"
                flat
                class="px-4 pb-2"
              >
                <v-data-table
                  v-if="dashboardData"
                  :headers="headers"
                  :items="dashboardData.getDashboardInfo.gamesRevenue"
                  :items-per-page="5"
                  no-data-text="Статистика по режимам отсутствует"
                />
              </v-card>
            </v-col>
          </v-row>

          <!-- No result -->
          <AppNotification
            v-else
            message="Нет результата :("
          />
        </template>
      </ApolloQuery>
    </v-col>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';
import DatePicker from '@/components/DatePicker.vue';
import AppNotification from '@/components/AppNotification.vue';
import InfoCard from '@/components/InfoCard.vue';
import PieBase from '@/components/PieBase.vue';

export default {
  components: {
    AppRow,
    DatePicker,
    AppNotification,
    InfoCard,
    PieBase,
  },
  data() {
    return {
      dateFrom: '',
      dateTo: '',
      cards: [
        {
          text: 'Доход (NGR)',
          value: 'ngr',
        },
        {
          text: 'House Edge',
          value: 'houseEdge',
        },
        {
          text: 'Поступления (депозиты)',
          value: 'deposits',
        },
        {
          text: 'Расходы (выводы)',
          value: 'withdraws',
        },
        {
          text: 'Иные расходы (по умолч. промо)',
          value: 'promotions',
        },
      ],
      show: false,
      expenses: [
        {
          text: 'Бонусы',
          value: 'bonuses',
        },
        {
          text: 'Подарки (раздачи)',
          value: 'gifts',
        },
        {
          text: 'Реферальная система',
          value: 'referrals',
        },
        {
          text: 'Комиссия платёжной системы',
          value: 'commissions',
        },
      ],
      chartData: [
        {
          name: 'Режим 1',
          value: 30,
        },
        {
          name: 'Режим 2',
          value: 15,
        },
        {
          name: 'Режим 3',
          value: 8,
        },
      ],
      headers: [
        { text: 'Режим', value: 'gameMode', class: 'white--text text-subtitle-2' },
        { text: 'Кол-во ставок:', value: 'betsQty', class: 'white--text text-subtitle-2' },
        { text: 'Сумма ставок:', value: 'betsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Выиграно:', value: 'winsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Проиграно:', value: 'looseAmount', class: 'white--text text-subtitle-2' },
        { text: 'GGR:', value: 'ggr', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
    };
  },
  methods: {
    saveDates(dates) {
      [this.dateFrom, this.dateTo] = dates;
    },
  },
};
</script>
